import "./index.scss";
import {Switch,Route} from "react-router-dom";
import LazyLoad from "&/lazyload";
const Foot = LazyLoad(()=>import("~/Foot"));

export default class Index extends Component{
    render(){
        return (
            <div>
                <Switch>
                    <Route path="/index/tansuo" component={LazyLoad(()=>import("../tansuo"))}/>
                    <Route path="/index/xinyuandan" component={LazyLoad(()=>import("../xinyuandan"))}/>
                    <Route path="/index/gushi" component={LazyLoad(()=>import("../gushi"))}/>
                    <Route path="/index/shoujianxiang" component={LazyLoad(()=>import("../shoujianxiang"))}/>
                    <Route path="/index/wode" component={LazyLoad(()=>import("../wode"))}/>
                </Switch>
                <Foot></Foot>
            </div>
        )
    }
}